  /* background-image: linear-gradient(burlywood, brown); */
  /* bckground-color: burlywood; */
}
.huhu {
  background-image: repeating-linear-gradient(burlywood, yellow 10%, brown 20%);
  height: 100vh;
}

.App {
  margin: auto;
  width: 50%;
  padding: 10px;
  background-image: linear-gradient(rgb(94, 27, 27), burlywood);
  height: 100vh;
}

.flex {
  display: flex;
  justify-content: space-around;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.img {
  max-width: 300px;
  max-height: 300px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
